<template>
	<view class="footer">
		<view class="showTitle">
			<view class="vrshow" v-for="(item,index) in boottomList" :key='index'>
				<view class="">
					<text class="iconfont" :class="item.icon"></text>
				</view>
				<text>
					{{item.title}}
				</text>
			</view>
		</view>
		<view class="footerBtn">
			<view class="threeShow" @click="Jsshow">
				三维显示
			</view>
			<view class="selectShow" @click="confirmSelect">
				选择三维图形
			</view>
		</view>
		<!-- popUp -->
		<view class="popUpBox">
			<uni-popup id="popupShare" ref="popupShare" type="share" @change="change">
				<view class="poplist">
					<view class="pop_header">
						<uni-segmented-control :current="current" :values="items" @clickItem="onClickItem" style-type='text' activeColor="#86B0D4"></uni-segmented-control>
					</view>
					<view class="content">
						 <view>
							<scroll-view scroll-y="true" class="classifyItem">
								<view v-show="current === 0">
									<scroll-view scroll-y="true" class="classifyItem">
										<view class="content_item" v-for="(item,index) in Alllist" :key='index'>
											<text @click="changePicAll(item.id)" :class="item.id==boolIndexAll?'textColor':''">{{item.name||'暂无数据'}}</text>
										</view>
									</scroll-view>
								</view>
								<view v-show="current === 1">
									<scroll-view scroll-y="true" class="classifyItem">
										<view class="content_item" v-for="(item,index) in Guidelist" :key='index'>
											<text @click="changePicGuide(item.id)" :class="item.id==boolIndexGuide?'textColor':''">{{item.name||'暂无数据'}}</text>
										</view>
									</scroll-view>
								</view>
								<view v-show="current === 2">
									<scroll-view scroll-y="true" class="classifyItem">
										<view class="content_item" v-for="(item,index) in Instrumentlist" :key='index'>
											<text @click="changePicInstru(item.id)" :class="item.id==boolIndexInstru?'textColor':''">{{item.name||'暂无数据'}}</text>
										</view>
									</scroll-view>
								</view>
							</scroll-view>
						</view>
					</view>
				</view>
			</uni-popup>
		</view>
	</view>
</template>

<script>
	export default{
		data(){
			return{
				// 底部导航
				boottomList:[
					{icon:'iconzhuanfa',title:'转发'},
					{icon:'iconshebeiguanli',title:'VR显示'},
				],
				current:0,
				items: ['匹配情况','导板','器械'],
				boolIndexAll:0,
				boolIndexGuide:0,
				boolIndexInstru:0,
				
			}
		},
		props:{
			Alllist:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			Guidelist:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			Instrumentlist:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			surcigal:{
				type:Object,
				default:()=>{
					return{}
				}
			},
			methodsStlId:{
				type:String,
				default:''
			}
			
			
		},
		methods:{
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			},
			change(e){},
			// 三维显示
			Jsshow(){
				console.log(this.methodsStlId)
				if(!this.methodsStlId){
					uni.showToast({
						title:'无相关文件',
						icon:'none'
					})
					return;
				}
				var baseUrl='https://technology.shoyii.com/?cos'+'='+true+'&userType=D'+'&token='+'&modelType='+'&titlename='+'&fileId='+this.methodsStlId
				var url=encodeURIComponent(baseUrl)
				uni.navigateTo({
					url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+url,
					animationType:'pop-in'
				})	
			},
			// 选择三维图形
			confirmSelect(){
				this.$refs.popupShare.open()
			},
			changeData(data,id){
				console.log(data)//分别对应的导板，器械和匹配情况的数据
				var a=data.filter(item=>{
					if(item.id==id){
						return item
					}
				})
				console.log(a)
				this.stlId=a[0].stlModelIds//点击title，所获得对应的stlid；用于进入3js页面
				console.log(this.stlId)
				var titleName=a[0].name
				var b=a[0].stlPictureModelIds.split(',')
				var indexArr=b.map(item=>{//遍历出匹配的下标
					return Object.keys(this.surcigal.previewImage).indexOf(item)
				})
				var newArr=[]
				indexArr.filter(item=>{
					newArr.push(Object.values(this.surcigal.previewImage)[item])
				})
				console.log(newArr)
				this.bannerList=newArr
				console.log(this.bannerList)
				this.bus.$emit('methodsData',{bannerList:this.bannerList,methodsStlId:this.stlId,titleName:titleName})//替换轮播图
			},
			// 点击title,更换图片
			changePicAll(id){
				console.log('321')
				console.log(id)
				this.boolIndexAll=id
				this.changeData(this.Alllist,id)
			},
			changePicGuide(id){
				this.boolIndexGuide=id
				this.changeData(this.Guidelist,id)
			},
			changePicInstru(id){
				this.boolIndexInstru=id
				this.changeData(this.Instrumentlist,id)
			}
		},
	}
</script>

<style scoped>
	.footer{
		height:110rpx;
		border-top:1px solid #EEEEEE;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 30rpx;
		width: 100%;
		background: #FFFFFF;
	}
	.showTitle{
		width: 45%;
		display: flex
	}
	.vrshow{
		color: #000000;
		font-size: 20rpx;
		text-align: center;
		width: 130rpx;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.vrshow .iconfont{
		font-size: 36rpx;
	}
	.footerBtn{
		width: 100%;
		height: 80rpx;
		display: flex;
		justify-content: space-between;
		line-height: 80rpx;
		margin-left: 20rpx;
		flex: 1;
	}
	.footerBtn uni-view{
		flex: 1;
		border: 1px solid #86B0D4;
		color: #86B0D4;
		text-align: center;
		font-size: 24rpx;
		font-family: PingFang-SC-Medium;
		font-weight: 500;
	}
	.threeShow{
		border-top-left-radius: 40rpx;
		border-bottom-left-radius: 40rpx;
	}
	.selectShow{
		border-top-right-radius: 40rpx;
		border-bottom-right-radius: 40rpx;
		background-color: #86B0D4;
		color: #FFFFFF!important;
	}
	.poplist{
		padding: 0 30rpx;
		height: 715rpx;
		background: #FFFFFF;
		/* display: flex;
		flex-direction: column; */
	}
	.poplist .content{
		color:#333333;
		font-size: 30rpx;
	}
	.poplist .content_item{
		border-bottom: 1px solid #EEEEEE;
		text-indent: 10rpx;
		padding: 25rpx 0;
	}
	.poplist .content_item uni-text{
		display: block;
	}
	.pop_header{
		border-bottom: 1px solid #EEEEEE;
		padding: 10rpx 0;
	}
	.classifyItem{
		/* flex: 1; */
		height: 610rpx;
		/* overflow: auto; */
	}
	.textColor{
		color: #86B0D4;
	}
</style>
